"use client";

import * as React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { ShoppingBag, Menu, X, User, LogOut, Settings } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { useSession, signOut } from "next-auth/react";
import { LoginModal } from "@/components/auth/LoginModal";
import { cn } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";

export function Navigation() {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = React.useState(false);
    const [isLoginOpen, setIsLoginOpen] = React.useState(false);
    const [isUserMenuOpen, setIsUserMenuOpen] = React.useState(false);

    const pathname = usePathname();
    const { cartCount, setIsCartOpen } = useCart();
    const { data: session } = useSession();
    const [scrolled, setScrolled] = React.useState(false);

    React.useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20);
        };
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    React.useEffect(() => {
        setIsMobileMenuOpen(false);
        setIsUserMenuOpen(false);
    }, [pathname]);

    const navLinks = [
        { name: "Home", href: "/" },
        { name: "My Account", href: "/account" },
        { name: "Catalog", href: "/products" },
        { name: "Contact", href: "/contact" },
    ];

    const handleSignOut = () => {
        signOut();
        setIsUserMenuOpen(false);
    };

    return (
        <>
            <header
                className={cn(
                    "fixed top-0 left-0 right-0 z-40 transition-all duration-300 border-b border-transparent",
                    scrolled ? "bg-white/80 backdrop-blur-[12px] border-border/50 py-4 supports-[backdrop-filter]:bg-white/60" : "bg-transparent py-6"
                )}
            >
                <div className="container-custom mx-auto">
                    <div className="flex items-center justify-between">
                        {/* Mobile Menu Button */}
                        <button
                            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                            className="md:hidden p-2 -ml-2 text-primary hover:text-black transition-colors"
                        >
                            {isMobileMenuOpen ? (
                                <X className="w-6 h-6" />
                            ) : (
                                <Menu className="w-6 h-6" />
                            )}
                        </button>

                        {/* Desktop Navigation (Left split) */}
                        <nav className="hidden md:flex items-center gap-8">
                            {navLinks.map((link) => (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    className="text-sm font-medium text-text-secondary hover:text-primary tracking-wide uppercase"
                                >
                                    {link.name}
                                </Link>
                            ))}
                        </nav>

                        {/* Logo (Centered) */}
                        <Link
                            href="/"
                            className="absolute left-1/2 -translate-x-1/2 md:static md:translate-x-0 font-display font-black text-3xl tracking-tighter"
                        >
                            intru
                        </Link>

                        {/* Actions */}
                        <div className="flex items-center gap-2 md:gap-4">
                            {session ? (
                                <div className="hidden md:block relative">
                                    <button
                                        onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                                        className="text-sm font-medium hover:text-primary transition-colors uppercase"
                                    >
                                        Account
                                    </button>
                                    <AnimatePresence>
                                        {isUserMenuOpen && (
                                            <motion.div
                                                initial={{ opacity: 0, y: 10 }}
                                                animate={{ opacity: 1, y: 0 }}
                                                exit={{ opacity: 0, y: 10 }}
                                                className="absolute right-0 top-full mt-4 w-48 bg-white border border-border p-2 z-50 shadow-sm"
                                            >
                                                <div className="px-3 py-2 text-xs text-text-tertiary border-b border-border/50 mb-1">
                                                    {session.user?.email}
                                                </div>
                                                <button
                                                    onClick={handleSignOut}
                                                    className="w-full text-left px-3 py-2 text-sm hover:bg-surface-elevated transition-colors"
                                                >
                                                    Log out
                                                </button>
                                            </motion.div>
                                        )}
                                    </AnimatePresence>
                                </div>
                            ) : (
                                <button
                                    onClick={() => setIsLoginOpen(true)}
                                    className="hidden md:block text-sm font-medium text-text-secondary hover:text-primary transition-colors tracking-wide uppercase"
                                >
                                    Log in
                                </button>
                            )}

                            <button
                                onClick={() => setIsCartOpen(true)}
                                className="relative p-2 text-primary hover:text-black transition-colors"
                                aria-label="Open Cart"
                            >
                                <span className="hidden md:inline mr-2 text-sm font-medium uppercase">Cart</span>
                                <ShoppingBag className="w-5 h-5 md:hidden" />
                                <span className="absolute -top-1 -right-1 md:top-0 md:right-0 md:relative md:bg-transparent md:text-primary md:w-auto md:h-auto w-4 h-4 bg-primary text-white text-[10px] md:text-sm font-bold flex items-center justify-center rounded-full">
                                    {cartCount > 0 ? `(${cartCount})` : "(0)"}
                                </span>
                            </button>
                        </div>
                    </div>
                </div>

                {/* Mobile Navigation */}
                <AnimatePresence>
                    {isMobileMenuOpen && (
                        <motion.div
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: "100vh" }}
                            exit={{ opacity: 0, height: 0 }}
                            className="md:hidden fixed top-[60px] left-0 right-0 bg-white border-t border-border z-50"
                        >
                            <div className="flex flex-col p-6 space-y-6">
                                {navLinks.map((link) => (
                                    <Link
                                        key={link.name}
                                        href={link.href}
                                        className="text-2xl font-display font-bold uppercase"
                                        onClick={() => setIsMobileMenuOpen(false)}
                                    >
                                        {link.name}
                                    </Link>
                                ))}
                                {!session && (
                                    <button
                                        onClick={() => {
                                            setIsMobileMenuOpen(false);
                                            setIsLoginOpen(true);
                                        }}
                                        className="text-2xl font-display font-bold uppercase text-left"
                                    >
                                        Log in
                                    </button>
                                )}
                            </div>
                        </motion.div>
                    )}
                </AnimatePresence>
            </header>

            <LoginModal isOpen={isLoginOpen} onClose={() => setIsLoginOpen(false)} />
        </>
    );
}
