import * as React from "react";
import { cn } from "@/lib/utils";
import { motion, HTMLMotionProps } from "framer-motion";

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    variant?: "primary" | "secondary" | "outline" | "ghost" | "danger";
    size?: "sm" | "md" | "lg" | "icon";
    isLoading?: boolean;
    leftIcon?: React.ReactNode;
    rightIcon?: React.ReactNode;
    asMotion?: boolean;
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
    ({
        className,
        variant = "primary",
        size = "md",
        isLoading = false,
        leftIcon,
        rightIcon,
        children,
        disabled,
        asMotion = true,
        ...props
    }, ref) => {

        const variants = {
            primary: "bg-primary text-white hover:bg-primary-dark shadow-md hover:shadow-lg hover:shadow-primary/20",
            secondary: "bg-secondary text-white hover:bg-secondary-dark shadow-md hover:shadow-lg hover:shadow-secondary/20",
            outline: "border-2 border-primary text-primary hover:bg-primary/5",
            ghost: "text-text-primary hover:bg-surface-elevated",
            danger: "bg-error text-white hover:bg-red-600 shadow-md",
        };

        const sizes = {
            sm: "h-9 px-3 text-sm",
            md: "h-11 px-6 text-base",
            lg: "h-14 px-8 text-lg",
            icon: "h-10 w-10 p-2 flex items-center justify-center",
        };

        const baseStyles = "inline-flex items-center justify-center rounded-lg font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-primary/50 disabled:opacity-50 disabled:pointer-events-none active:scale-95";

        const content = (
            <>
                {isLoading && (
                    <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-current" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                )}
                {!isLoading && leftIcon && <span className="mr-2">{leftIcon}</span>}
                {children}
                {!isLoading && rightIcon && <span className="ml-2">{rightIcon}</span>}
            </>
        );

        if (asMotion) {
            // Using a wrapper or casting motion.button props if strictly needed, 
            // but for simplicity in this specific typing setup, we'll render a motion.button
            // Note: React.forwardRef + Framer Motion sometimes requires specific handling for refs.
            // For this simplified version, we'll stick to standard button but include motion classes if needed or valid motion props.
            // Ideally, use motion.button directly in usage, but here's a hybrid approach:
            return (
                <motion.button
                    ref={ref as any}
                    whileHover={{ scale: disabled || isLoading ? 1 : 1.02 }}
                    whileTap={{ scale: disabled || isLoading ? 1 : 0.98 }}
                    className={cn(baseStyles, variants[variant], sizes[size], className)}
                    disabled={disabled || isLoading}
                    {...(props as any)}
                >
                    {content}
                </motion.button>
            );
        }

        return (
            <button
                ref={ref}
                className={cn(baseStyles, variants[variant], sizes[size], className)}
                disabled={disabled || isLoading}
                {...props}
            >
                {content}
            </button>
        );
    }
);

Button.displayName = "Button";

export { Button };
